.review {
  background-color: #f7f7f7;
  justify-content: center;
  align-items: center;
  width: fit-content;
  height: fit-content;
  padding: 30px 14px;
  display: flex;
}

.review__svg {
  display: none;
}

.review__text {
  text-align: left;
  color: #a9afb1;
  margin: 0;
  padding: 0;
  font-family: Raleway, sans-serif;
  font-size: 14px;
  font-style: italic;
  font-weight: 500;
}

.review__mini-separator {
  width: 48px;
  margin: 0 38px 0 0;
}

.review__signature {
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 40px;
  font-family: Kaushan Script, sans-serif;
  font-size: 18px;
  font-weight: 400;
  display: flex;
}

.review__person {
  height: 36px;
  margin: 18px 0 0;
  padding: 0;
}

.swiper-pagination-bullet {
  z-index: 10;
  box-sizing: border-box;
  opacity: 1 !important;
  background: none !important;
  border: 1px solid #c6cbcd !important;
  width: 9px !important;
  height: 9px !important;
  margin: 0 10px 28px !important;
}

.swiper-pagination-bullet-active {
  background-color: #c6cbcd !important;
}

.swiper {
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.swiper__review {
  background-color: #f8f8f8;
  margin: 0 auto;
}

.review-two {
  background-color: #fff;
}

.swiper-slide {
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.work-review {
  background-color: #fff;
  flex-direction: column;
}

.review__img {
  margin-top: 30px;
  margin-bottom: 40px;
}

.work-review .review__signature {
  height: 36px;
  margin-top: 14px;
}

.work-review .review__person {
  margin: 0;
}

